@import url(https://fonts.googleapis.com/css?family=Roboto:100,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* grid setup area */
.section1{
    background-image: url(/kylerdaybelldotcom/static/media/BWCode.b7cc83bf.jpg);
    background-color: rgba(38, 35, 51,0.8);
    background-blend-mode: darken;
    background-size: cover;
    padding: 2vh;
    min-height: 50vh;
}



.section2{
    background-image: url(/kylerdaybelldotcom/static/media/Squares.9b704014.jpg);
    background-color: rgba(38, 35, 51,0.8);
    background-blend-mode: darken;
    background-size: cover;
    padding: 2vh;
    min-height: 50vh;
}

.section3{
    background-image: url(/kylerdaybelldotcom/static/media/Cracks.87f56b3d.jpg);
    background-color: rgba(38, 35, 51,0.8);
    background-blend-mode: darken;
    background-size: cover;
    padding: 2vh;
    min-height: 50vh;
}


.header-text{
    grid-area: image;
    justify-self: center;
    align-self: center;
    text-align:center;
    color:#ccd1dd;
    font-weight: 100;
    font-size: calc(20px + 4vmin);
    z-index: 1!important;
}

.cards{
    display:grid;
    grid-template-columns:30% 40% 30%;
    grid-template-rows:50vh;
    grid-template-areas: 
    "card1 card2 card3";
    z-index: 2;
}

.card-third1{
    grid-area: card1;
    padding:5vh;
}

.card-third2{
    grid-area: card2;
    padding:5vh;
}
.card-third3{
    grid-area: card3;
    padding:5vh;
}

.image-header-image3{
    grid-area: image3;
    width:100%;
    height:100%;
    opacity: .5;
    z-index: -1;
}

.card{
    text-align:center;
    color:#ccd1dd;
    background-color:#262333;
    padding:1vh;
    height:40vh;
}

.card img{
    max-height: 75%;
    max-width: 65%;
}

.card:hover h1{
    color:#31e184;
}

/* this is below the first image  */
.image-header1{
    grid-area: image1;
}

.image-header-image1{
    grid-area: image1;
    width:100%;
    height:100%;
    opacity: .5;
    z-index: -1;
}

.header-text1{
    grid-area: skillstitle;
    justify-self: center;
    align-self: start;
    color:#ccd1dd;
    font-weight: 100;
    font-size: calc(20px + 4vmin);
    z-index: 1!important;
}

/* skills sub grid */
.skills{
    grid-area: image1;
    display: grid;
    grid-template-columns: subgrid;
    grid-template-columns:20% 60% 20%;
    grid-template-rows:10vh 5vh;
    grid-template-areas: 
    "skillstitle skillstitle skillstitle"
    "skill1name skill1bar skill1percentage"
    "skill2name skill2bar skill2percentage"
    "skill3name skill3bar skill3percentage"
    "skill4name skill4bar skill4percentage"
    "skill5name skill5bar skill5percentage"
    "skill6name skill6bar skill6percentage";
    z-index: 2;
}

.green{
    color:#31e184;
}

