/* text settings */
.roboto-text-thick{
    font-family: 'Roboto', sans-serif;
    -webkit-text-emphasis: thin;
            text-emphasis: thin;
    font-weight:700;
}

.roboto-text-thin{
    font-family: 'Roboto', sans-serif;
    -webkit-text-emphasis: thin;
            text-emphasis: thin;
    font-weight:100;
}

.page-text{
    margin:auto;
    margin-bottom:2vh;
    padding:1vh;
    background-color: grey;
    color:white;
    width: 80%;
}

